@import './var';
.menuContentBox {
  margin: 1rem auto;
  h1 {
    /* ${props => props.theme.fontLeckerli} */
    // font-family: $fontRobotoSlab;
    // font-size: 40px;
    color: $darkNavy;
    text-align: center;
    font-family: $fontAmatic;
    font-size: 64px;
    font-weight: 700;
    line-height: 1.4;
    @media (max-width: 767px) {
      font-size: 44px;
    }
  }
  .itemsBox {
    margin-top: 10px;
    padding: 20px;
    /* background: rgba(0, 0, 0, 0.5); */
    @media (max-width: 767px) {
      padding: 10px;
    }
    .itemBoxGrid {
      display: grid;
      grid-gap: 20px;
      grid-template-columns: 1fr 1fr;

      @media (max-width: 767px) {
        grid-gap: 10px;
        grid-template-columns: 1fr;
      }

      /* grid-template-columns: repeat(auto-fit, minmax(400px, 1fr)); */

      .itemBox {
        /* background: rgba(255, 0, 0, 0.5); */
        display: flex;
        justify-content: center;
        .imageBox {
          overflow: hidden;
          width: 160px;
          height: 160px;
          border-radius: 80px;
          @media (max-width: 1023px) {
            width: 140px;
            height: 140px;
            border-radius: 70px;
          }
          /* width: 200px;
          height: 140px;
          border-radius: 8px; */
          /* position: relative; */
          img {
            /* position: absolute;
            left: 50%;
            transform: translateX(-50%); */
            -webkit-transform: translateZ(0);
            -webkit-perspective: 1000;
            -webkit-backface-visibility: hidden;
            width: 160px;
            height: 160px;
            border-radius: 80px;
            user-select: none;
            @media (max-width: 767px) {
              width: 140px;
              height: 140px;
              border-radius: 70px;
            }
          }
        }
        .contentBox {
          flex: 1;
          margin-left: 20px;
          display: flex;
          flex-direction: column;
          justify-content: center;
          @media (max-width: 767px) {
            margin-left: 10px;
          }
          .name {
            position: relative;
            // font-family: $fontPlayfair;
            font-family: $fontRobotoSlab;
            // ${props => props.theme.fontPlayfair}
            color: $black;
            font-weight: bold;
            font-size: 18px;
            line-height: 1.5;
            .day {
              position: absolute;
              font-size: 12px;
              font-style: italic;
              // margin-left: 10px;
              margin-top: -12px;
              top: 0;
              color: green;
              @media (max-width: 565px) {
                // margin-left: -22px;
                // font-size: 12px;
                margin-top: -16px;
                left: 30%;
              }
            }
          }
          .price {
            font-size: 18px;
            font-weight: bold;
            font-family: $fontPlayfair;
            // ${props => props.theme.fontPlayfair}
            // color: ${props => props.theme.darkRed};

            color: $darkRed;
            .striked {
              color: lighten($darkRed, 8%);
              text-decoration: line-through;
            }
          }
          .description {
            color: $grey;
            font-style: italic;
            line-height: 1.2;
          }
        }
      }
    }
  }
}
